@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
.b-page{
	position: relative;
	overflow-x: hidden;
	z-index: 10;
	&__header{
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 10;
	}
	&__main{
		min-height: calc(100vh - 320px);
		padding-top: 170px;
		position: relative;
		z-index: 5;
	}
	&__footer{
		position: relative;
		z-index: 2;
	}
}
.b-cart-open{
	width: 463px;
	min-height: 100vh;
	padding: 45px 67px 189px 30px;
	background: $color-white;
	box-shadow: 0 0 23px rgba(0, 0, 0, .1);
	transition: left 0.3s ease;
	position: absolute;
	top: 0;
	left: calc(100% + 463px);
	z-index: 20;
	&__tit{
		margin-bottom: 19px;
		padding: 1px 0 0 25px;
		display: block;
		font-size: 10px;
		font-weight: 500;
		line-height: 14px;
		text-transform: uppercase;
		color: $color-body;
		position: relative;
		@extend .icon;
	    @extend .icon-cart;
		&:before{
			display: block;
			font-size: 12px;
			line-height: 12px;
			transform: translate(0, -50%);
			position: absolute;
			top: 50%;
			left: 0;
		}
	}
	&__foot{
		position: absolute;
		bottom: 69px;
		left: 30px;
		right: 67px;
		&-total{
			padding: 0 0 23px;
			text-align: right;
			span{
				padding-left: 3px;
				font-size: 24px;
				font-weight: 500;
				color: $color-body;
			}
		}
		&-col{
			width: calc(50% - 15px);
			float: right;
			&:first-child{
				float: left;
			}
		}
		.b-btn{
			width: 100%;
			min-width: 100%;
		}
	}
	&.is-open{
		left: calc(100% - 463px);
	}
}
.b-cart-post{
	padding: 19px 0 17px;
	border-bottom: 1px solid $color-greyL;
	position: relative;
	&__img{
		width: 60px;
		height: 60px;
		margin-right: 10px;
		padding: 8px;
		float: left;
		border: 1px solid $color-greyL;
		line-height: 42px;
		text-align: center;
		position: relative;
		img{
			height: 100%;
			display: inline-block;
			vertical-align: middle;
		}
	}
	&__txt{
		width: calc(100% - 86px);
		padding: 5px 10px 0 0;
		float: left;
		font-size: 12px;
		line-height: 18px;
		position: relative;
		&-tit{
			padding: 0 0 3px;
			font-weight: 500;
			color: $color-body;
		}
		&-sum{
			width: 50%;
			float: left;
		}
		&-price{
			width: 50%;
			float: right;
			color: $color-body;
			text-align: right;
		}
	}
	&__btn{
		width: 16px;
		margin-right: -2px;
		padding: 7px 0 0 0;
		float: right;
		position: relative;
	}
}
// HEADER
.b-header{
	background: $color-white;
	box-shadow: 0 1px 7px rgba(0, 0, 0, .1);
	transition: all 0.3s ease;
	&__top{
		width: 100%;
		padding: 26px 0 28px;
		border-bottom: 1px solid $color-greyL;
		transition: all 0.3s ease;
		position: relative;
		@extend .b-flex;
        @extend .b-flex--bottom;
        @extend .b-flex--space-between;
        &-logo{
        	position: relative;
        	z-index: 1;
        }
		&-info{
			white-space: nowrap;
			position: relative;
			z-index: 2;
		}
	}
	&__bot{
		width: 100%;
		height: 70px;
		transition: all 0.3s ease;
		position: relative;
		@extend .b-flex;
        @extend .b-flex--middle;
        @extend .b-flex--space-between;
	}
	&__search,
	&__cart{
		padding: 0 0 2px 33px;
		display: inline-block;
		vertical-align: middle;
	}
	&__search{
		width: 110px;
	}
	&.fix{
		box-shadow: 0 1px 7px rgba(0, 0, 0, .1);
		.b-header__top{
			padding: 10px 0 4px;
			&-logo{
				padding: 0 0 7px;
			}
		}
		.b-header__bot{
			height: 50px;
		}
		.b-logo{
			width: 67px;
			height: 27px;
		}
	}
}
.page-home{
	.b-header{
		box-shadow: none;
		&.fix{
			box-shadow: 0 1px 7px rgba(0, 0, 0, .1);
		}
	}
}
.b-logo{
	width: 110px;
	height: 46px;
	transition: all 0.3s ease;
	position: relative;
	a{
		width: 100%;
		height: 100%;
		display: block;
		background-image: url(../images/svg/logo.svg);
		background-position: 0 0;
		background-repeat: no-repeat;
		background-size: 100% 100%;
		text-indent: -9999em;
		overflow: hidden;
		position: absolute;
		top: 0;
		left: 0;
	}
	&--grey{
		a{
			background-image: url(../images/svg/logo-grey.svg);
		}
	}
}
.b-search{
	width: 72px;
	height: 30px;
	padding: 10px 0;
	transition: all 0.3s ease;
	position: relative;
	overflow: hidden;
	&__btn{
		padding: 0 0 0 25px;
		display: block;
		font-size: 11px;
		font-weight: 500;
		line-height: 12px;
		text-transform: uppercase;
		color: $color-body;
		transition: all 0.3s ease;
		cursor: pointer;
		position: relative;
		right: 0;
		z-index: 1;
		@extend .icon;
	    @extend .icon-search;
		&:before{
			display: block;
			font-size: 12px;
			line-height: 12px;
			transform: translate(0, -50%);
			position: absolute;
			top: 50%;
			left: 0;
		}
	}
	&__form{
		width: 417px;
		transition: all 0.3s ease;
		position: absolute;
		top: 0;
		right: -417px;
		z-index: 2;
		.b-search-form{
			&__btn{
				&:before{
					opacity: 0;
				}
			}
		}
	}
	&.is-open{
		width: 417px;
		margin-left: calc(72px - 417px);
		.b-search__btn{
			right: -395px;
		}
		.b-search__form{
			right: 0;
		}
	}
}
.b-search-form{
	height: 30px;
	position: relative;
	&__input{
		width: 100%;
		padding: 5px 30px 5px 10px;
		display: block;
		border: none;
		border-top: 1px solid transparent;
		border-bottom: 1px solid $color-greyL;
		background: none;
		font-size: 10px;
		font-weight: 500;
		line-height: 18px;
		letter-spacing: 1px;
		color: $color-grey;
		position: relative;
		z-index: 1;
	}
	&__btn{
		padding: 8px;
		border: none;
		background: none;
		display: block;
		transform: translate(0, -50%);
		cursor: pointer;
		position: absolute;
		top: 50%;
		right: 0;
		z-index: 2;
		@extend .icon;
	    @extend .icon-search;
		&:before{
			display: block;
			font-size: 12px;
			line-height: 12px;
			position: relative;
		}
	}
}
.b-cart{
	padding: 1px 24px 0 25px;
	display: inline-block;
	font-size: 11px;
	font-weight: 500;
	line-height: 14px;
	text-transform: uppercase;
	color: $color-body;
	cursor: pointer;
	position: relative;
	@extend .icon;
    @extend .icon-cart;
	&:before{
		display: block;
		font-size: 12px;
		line-height: 12px;
		transform: translate(0, -50%);
		position: absolute;
		top: 50%;
		left: 0;
	}
	span{
		min-width: 14px;
		height: 14px;
		padding: 0 2px 0 3px;
		display: block;
		background: $color-green;
		color: $color-white;
		text-align: center;
		transform: translate(0, -50%);
		position: absolute;
		top: 50%;
		right: 0;
	}
}
.b-menu{
	font-size: 11px;
	font-weight: 500;
	line-height: 10px;
	text-transform: uppercase;
	position: relative;
	.b-search-form{
		max-width: 300px;
		margin: 0 auto;
		display: none;
	}
	&__btn{
		width: 26px;
		height: 26px;
		display: none;
		cursor: pointer;
		position: relative;
		z-index: 6;
		span{
			width: calc(100% - 5px);
			height: 1px;
			display: block;
			background: $color-body;
			transform: translate(0, -50%);
			position: absolute;
			top: 50%;
			left: 0;
			&:before,
			&:after{
				height: 1px;
				display: block;
				content: '';
				background: $color-body;
				transition: all 0.3s ease;
				position: absolute;
				left: 0;
			}
			&:before{
				top: -6px;
				right: 5px;
			}
			&:after{
				bottom: -6px;
				right: -5px;
			}
		}
		&:hover{
			span{
				background: $color-green;
				&:before,
				&:after{
					background: $color-green;
					right: 0;
				}
			}
		}
		&.is-open{
			span{
				width: 100%;
				background-color: transparent;
				&:after,
				&:before{
					top: 0;
					right: 0;
				}
				&:before{
					transform: rotate(-45deg);
				}
				&:after{
					transform: rotate(45deg);
				}
			}
		}
	}
	&__list{
		white-space: nowrap;
		position: relative;
	}
	&__item{
		padding: 0 0 0 34px;
		display: inline-block;
		vertical-align: middle;
		position: relative;
		a{
			display: inline-block;
			color: $color-body;
			text-decoration: none;
			position: relative;
			z-index: 2;
			&:before{
				width: 0;
				height: 2px;
				display: block;
				content: '';
				background: $color-green;
				transition: all 0.3s ease;
				position: absolute;
				bottom: 1px;
				left: 50%;
				z-index: 1;
			}
			span{
				display: inline-block;
				position: relative;
				z-index: 2;
			}
			&:hover{
				color: $color-body;
				&:before{
					width: 100%;
					left: 0;
				}
			}
		}
		&:first-child{
			padding-left: 0;
		}
		&:hover,
		&.is-active{
			a{
				&:before{
					width: 100%;
					left: 0;
				}
			}
		}
	}
}
.b-phone{
	font-size: 11px;
	font-weight: 500;
	line-height: 10px;
	text-transform: uppercase;
	color: $color-grey;
	white-space: nowrap;
	position: relative;
	span{
		padding-left: 20px;
		display: inline-block;
		vertical-align: top;
		white-space: nowrap;
	}
	a{
		color: $color-grey;
		text-decoration: none;
	}
}
// FOOTER
.b-footer{
	&__top{
		padding: 20px 0;
		font-size: 10px;
		font-weight: 500;
		line-height: 12px;
		color: $color-body;
		text-transform: uppercase;
		position: relative;
		@extend .b-flex;
        @extend .b-flex--bottom;
        @extend .b-flex--space-between;
		&-link{
			width: calc(100% - 50px);
			padding: 0 0 5px;
			i{
				padding: 0 5px;
				display: inline-block;
				vertical-align: middle;
				font-size: 12px;
				color: $color-grey;
				&.icon-visa,
				&.icon-mir{
					font-size: 9px;
				}
			}
		}
		&-logo{
			width: 100px;
			.b-logo{
				margin: 0 auto;
			}
		}
		&-share{
			width: calc(100% - 50px);
			padding: 0 0 5px;
			text-align: right;
		}
	}
	&__bot{
		padding: 26px 0;
		border-top: 1px solid $color-greyL;
		font-size: 10px;
		font-weight: 300;
		line-height: 12px;
		text-align: center;
		color: $color-grey;
		position: relative;
	}
	a{
		text-decoration: none;
	}
}
.b-share{
	white-space: nowrap;
	position: relative;
	&__tit{
		padding-right: 10px;
		display: inline-block;
		vertical-align: middle;
	}
	&__list{
		display: inline-block;
		vertical-align: middle;
	}
	&__item{
		padding: 0 0 0 10px;
		display: inline-block;
		vertical-align: middle;
		&:first-child{
			padding-left: 0;
		}
	}
	a{
		font-size: 12px;
		color: $color-grey;
		text-decoration: none;
		&:hover{
			color: $color-green;
		}
	}
	&--center{
		text-align: center;
	}
}
// MAIN
.b-main{
	padding-bottom: 100px;
}
// home page
.b-main-screen{
	padding: 110px 110px 30px;
	background: $color-light;
	position: relative;
	overflow: hidden;
	@extend .b-flex;
	&:before{
		width: 100%;
		height: 100%;
		display: block;
		content: '';
		background: right center url(../images/svg/letters-main.svg) no-repeat;
		background-size: auto 100%;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 1;
	}
	&__txt{
		width: calc(100% - 287px);
		padding: 15px 0 15px 0;
		position: relative;
		z-index: 3;
		@extend .b-flex;
		@extend .b-flex--middle-col;
		&-tit{
			padding: 0 0 55px;
			font-size: 40px;
			font-weight: 500;
			line-height: 48px;
			text-transform: uppercase;
			color: $color-body;
		}
		&-entry{
			padding: 0 20px 65px 0;
			font-size: 14px;
			line-height: 30px;
		}
	}
	&__img{
		width: 445px;
		margin: 0 -43px 0 -115px;
		padding: 15px 0 0;
		position: relative;
		z-index: 2;
		@extend .b-flex;
		@extend .b-flex--bottom-col;
		img{
			width: 100%;
			display: block;
		}
	}
}
.b-our-plus{
	padding: 147px 0 0;
	text-align: center;
	position: relative;
	.b-head{
		padding: 0 0 51px;
	}
	&__row{
		margin: 0 -20px;
		position: relative;
	}
	&__col{
		width: 25%;
		padding: 30px 20px 0;
		float: left;
		position: relative;
	}
}
.b-plus-post{
	max-width: 200px;
	margin: 0 auto;
	text-align: center;
	position: relative;
	&__icon{
		height: 65px;
		margin: 0 0 16px;
		font-size: 39px;
		line-height: 65px;
		color: $color-green;
		.icon-plane{
			font-size: 32px;
		}
		.icon-delivery{
			font-size: 45px;
		}
		.icon-office{
			font-size: 35px;
		}
	}
	&__tit{
		padding: 0 0 11px;
		font-size: 14px;
		font-weight: 500;
		line-height: 18px;
		color: $color-body;
	}
	p{
		font-size: 12px;
		line-height: 18px;
	}
}
.b-action{
	padding: 140px 0 0;
	position: relative;
	.b-head{
		padding: 0 0 46px;
	}
	&__txt{
		width: calc(100% - 398px);
		padding: 43px 85px 0 0;
		float: left;
		position: relative;
		&-entry{
			height: 142px;
			margin-bottom: 40px;
			position: relative;
			overflow: hidden;
		}
	}
	&__img{
		width: 398px;
		padding: 0 0 18px;
		float: right;
		position: relative;
		&:before{
			width: 100%;
			display: block;
			content: '';
			background: $color-light;
			position: absolute;
			top: 50px;
			bottom: 0;
			left: 0;
			z-index: 1;
		}
		img{
			max-width: 232px;
			margin: 0 auto;
			display: block;
			position: relative;
			z-index: 2;
		}
	}
}
.b-popular{
	padding: 145px 0 0;
	position: relative;
	.b-head{
		padding: 0 0 3px;
	}
	&__row{
		text-align: center;
		position: relative;
	}
	&__col{
		width: 20%;
		margin: 0 -2px;
		display: inline-block;
		vertical-align: top;
	}
}
.b-prev-prod{
	padding: 31px 38px 27px;
	background: $color-white;
	transition: all 0.3s ease;
	text-align: center;
	position: relative;
	z-index: 1;
	&__img{
		height: 190px;
		margin-bottom: 52px;
		position: relative;
		img{
			height: 100%;
			margin: 0 auto;
			display: block;
		}
	}
	&__tit{
		height: 50px;
		margin-bottom: 24px;
		font-size: 14px;
		font-weight: 500;
		line-height: 24px;
		color: $color-body;
		position: relative;
		overflow: hidden;
		a{
			display: block;
			color: $color-body;
			text-decoration: none;
		}
		span{
			display: block;
		}
	}
	&__foot{
		width: 100%;
		height: 16px;
		line-height: 16px;
		position: relative;
		&-price{
			transition: all 0.3s ease;
			transform: translate(-50%, 0);
			position: absolute;
			top: 0;
			left: 50%;
			&-old{
				opacity: 0;
				transition: all 0.3s ease;
				transform: translate(calc((50% - 100%) + 15px), 0);
				text-decoration: line-through;
				position: absolute;
				top: 0;
				left: 50%;
			}
		}
		&-btn{
			transition: all 0.3s ease;
			opacity: 0;
			position: absolute;
			top: 0;
			right: 50%;
		}
		&:hover{
			.b-prev-prod__foot-price{
				transform: translate(0, 0);
				left: 0;
				&-old{
					opacity: 1;
				}
			}
			.b-prev-prod__foot-btn{
				opacity: 1;
				right: 0;
			}
		}
	}
	&:hover{
		margin-left: -10px;
		margin-right: -10px;
		padding-left: 48px;
		padding-right: 48px;
		box-shadow: 0 0 23px rgba(0, 0, 0, .1);
		z-index: 2;
		.b-prev-prod__foot{
			&-price{
				transform: translate(0, 0);
				left: 0;
				&-old{
					opacity: 1;
				}
			}
			&-btn{
				opacity: 1;
				right: 0;
			}
		}
	}
}
.b-news{
	padding: 125px 0 0;
	position: relative;
	.slick-arrow{
		position: absolute;
		bottom: 0;
		right: 275px;
		&.slick-prev{
			right: 335px;
		}
	}
}
.b-news-slid{
	position: relative;
	&__img{
		height: 500px;
		margin-right: 300px;
		background: $color-light;
		position: relative;
		overflow: hidden;
		z-index: 1;
		img{
			height: 100%;
			display: block;
		}
	}
	&__txt{
		width: 490px;
		padding: 65px 0 86px 95px;
		background: $color-white;
		position: absolute;
		bottom: 0;
		right: 0;
		z-index: 2;
		&-tit{
			padding: 0 0 47px;
			font-size: 24px;
			font-weight: 500;
			line-height: 26px;
			text-transform: uppercase;
			color: $color-body;
		}
		&-subtit{
			width: calc(100% - 80px);
			height: 12px;
			padding: 0 10px 0 0;
			float: left;
			font-size: 12px;
			font-weight: 500;
			line-height: 14px;
			text-transform: uppercase;
			color: $color-body;
			position: relative;
			overflow: hidden;
		}
		&-date{
			width: 80px;
			float: right;
			font-size: 12px;
			line-height: 14px;
			color: $color-grey;
			text-align: right;
		}
		&-entry{
			padding: 31px 0 0;
			a{
				color: $color-grey;
				text-decoration: underline;
				&:hover{
					color: $color-green;
				}
			}
		}
	}
}
.b-about{
	padding: 144px 0 0;
	position: relative;
	.b-head{
		padding: 0 0 89px;
	}
	p{
		padding: 0 0 22px;
	}
}
// sub page
.b-subpage-head{
	padding: 45px 0 42px;
	position: relative;
	@extend .b-flex;
	@extend .b-flex--middle;
